}
header{
    min-height: 156px;
    -webkit-transition: all 0.2s ease-in-out;
    transition: all 0.2s ease-in-out;
}

.HeaderTop {
    background: #1B4F72;
}

.HeaderTop .inner-top-line {
    -webkit-transition: all 0.2s ease-in-out;
    transition: all 0.2s ease-in-out;
    padding: 12px 0;
    overflow: hidden;
}

.HeaderTop ul.info-list li {
    display: inline-block;
    margin-right:30px;
    margin-bottom: 0;
    color: #fff;
    font-size: 15px;
    line-height: 41px;
    font-weight: 600;
    font-family: "Raleway", sans-serif;
}

.HeaderTop ul.info-list li i {
    font-size: 14px;
    margin-right:10px;
    color: #F7DC6F;
}

.HeaderTop ul.social-icons {
    padding:8px 0;
    float: right;
}

.HeaderTop ul.social-icons li {
    display: inline-block;
    margin-left: 16px;
}

.HeaderTop ul.social-icons li a {
    color: #fff;
    font-size: 15px;
}

.HeaderTop ul.social-icons li a:hover {
    color: #F7DC6F;
}

.sticky > .navbar {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 9;
    -webkit-transition: all 0.2s ease-in-out;
    transition: all 0.2s ease-in-out;
    box-shadow: 0 0 5px #ccc;
}

.navbar{
    padding: 0;
    background: #fff;
}
.navbar-expand-lg .navbar-nav .nav-link {
    color: #333333;
    font-size: 15px;
    font-family: "Raleway", sans-serif;
    font-weight: 700;
    text-transform: uppercase;
    -webkit-transition: all 0.2s ease-in-out;
    transition: all 0.2s ease-in-out;
    padding: 32px 15px;
}

.navbar-expand-lg .navbar-nav .nav-link:hover {
    color: #1B4F72
}


.form-search{
    display: none;
    width: 630px;
    padding: 14px;
    margin-bottom: -1px;
    background: #F7DC6F;
    position: absolute;
    top: 0;
    bottom: 1px;
    right: 0;
    overflow: hidden;
}

.searchBlock:hover .form-search{
    display: block;
}

.navbar-expand-lg .navbar-nav {
   position: relative;
}

.form-search button {
    background: transparent;
    border: none;
    float: right;
    margin-top: -45px;
    margin-right: -5px;
    position: relative;
    z-index: 2;
    outline: none;
    font-size: 15px;
}

.form-search input[type="text"] {
    margin: 0;
    color: #1B4F72;
    padding: 18px 20px;
    border: none;
    width: 100%;
    outline: none;
    background: transparent;
    font-family: "Raleway", sans-serif;
    font-weight: 600;
    color: #333333;
    -webkit-transition: all 0.2s ease-in-out;
    transition: all 0.2s ease-in-out; 
}
footer {
    background: #222222;
}

footer .up-footer {
