}

footer .up-footer .footer-widget > h3 span {
    display: inline-block;
    color: #F7DC6F;
}

footer .up-footer ul.social-icons {
    margin-top: 30px;
}

footer .up-footer ul.social-icons li {
    display: inline-block;
    margin-right: 5px;
}

footer .up-footer ul.social-icons li a {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    border: 2px solid #555555;
    text-align: center;
    line-height: 36px;
    font-size: 14px;
    color: #555555;
    display: block;
}

footer .up-footer ul.social-icons li a:hover {
    border: 2px solid transparent;
    background: #F7DC6F;
    color: #333333;
}

.footer-links li a{
