.itemBG{
    padding: 160px 0px;
    background-size: cover;
    background-position: center center;
}

.slick-arrow{
    position: absolute;
    top: 50%;
    z-index: 5;
    cursor: pointer;
    min-width: 70px;
    min-height: 70px;
    border-radius: 35px;
    overflow: hidden;
    background: rgba(0,0,0,.1);
    border: none;
    color: #fff;
}

.slick-arrow.slick-prev{
    left:0;
}


.slick-arrow.slick-next{
    right:0;
}

.itemBG h2{
    font-size:45px;
    line-height:1.3;
    color:#fff;
}

.itemBG p{
    color:#fff;
    font-size: 16px;
    margin: 20px 0;
}

.itemBG .button-one{
    background: #F7DC6F;
    border: 2px solid #F7DC6F;
    padding: 11px 25px;
    font-weight: 700;
    font-size: 11px;
}


.itemBG .button-two{
    border: 2px solid #fff;
    color: #fff;
    padding: 11px 25px;
    font-weight: 700;
    font-size: 11px;
    margin-right: 10px;
}
.Testimonial {
    padding: 100px 0;
    background: #1B4F72;
}

.Testimonial .test-content {
    margin-left: 140px;
}

.Testimonial i {
    float: left;
    display: inline-block;
